@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);

.body-donar {
    font-family: 'Lobster', cursive;
    height: 100vh;
    width: 100%;
    background: url(/DonarApp/static/media/grey.1bc720c6.jpg);
    display: flex;
    align-items: center;
    justify-content: center;

}

header {

    height: 80vh;
    width: 70%;
    background-color: white;
    background-image: url(/DonarApp/static/media/wave.656acad2.png);
    background-size: cover;
    background-position: right;
    background-repeat: no-repeat;
    border-radius: 20px;
    box-shadow: 2px 4px #000;
    overflow: hidden;
}

header nav {
    width: 90%;
    height: 15%;

    margin: auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    list-style-type: none;
}

header nav li {
    padding-right: 1rem;
}

header nav .brand {
    width: 7rem;
    height: 7rem;
}

header nav li a {
    text-decoration: none;
    color: grey;
    font-size: 1.2rem;
    transition: all .3s ease-in-out;
}

header nav li a:hover {
    color: #000;
}

header .main-container {

    width: 100%;
    height: 85%;
    display: flex;
    border: none;

}

header .main-container .context-bx,
.dish-bx {
    position: relative;
    width: 50%;
    height: 100%;
}

header .main-container .context-bx {
    padding-left: 60px;
    line-height: 1.6;
    display: flex;
    flex-direction: column;
    justify-content: center;

}


header .main-container .context-bx h1 {
    font-size: 1.8rem;
    color: #875708;
    transition: all .3 ease-in-out;

}

header .main-container .context-bx h3 {
    font-size: 2rem;
    color: rgb(91, 43, 4);
    transition: all .3 ease-in-out;

}

/* button properties */
header .main-container .context-bx button {
    width: 180px;
    padding: 8px 10px;
    border-radius: 30px;
    margin-top: 10px;
    /* border: 1px solid #131314; */
    border: none;
    background: #e69a20;
    color: #6D1D01;
    font-family: 'Lobster', cursive;
    box-shadow: 3px 5px 8px gray;
    font-weight: 500;
    font-size: 1.4rem;
}

header .main-container .context-bx button:hover {
    cursor: pointer;
    background: #6D1D01;
    color: white;

    -webkit-transform: scale(1.2);

            transform: scale(1.2);
}

header .main-container .dish-bx .dish1 {
    position: absolute;
    width: 55vh;
    height: 55vh;
    -webkit-filter: drop-shadow(5px 10px 10px #000);
            filter: drop-shadow(5px 10px 10px #000);
    top: -35px;
    -webkit-animation: spin 5s infinite;
            animation: spin 5s infinite;
}

@-webkit-keyframes spin {

    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {

    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

form {
    margin-top: 1rem;
    font-size: 1.2rem;
}

form input {
    padding: 0.5rem;
    margin-bottom: 1rem;
    margin-right: 1rem;
    font-size: 1.2rem;


}

.zoom {
    padding: 0.625rem;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    /* Animation */
    margin: 0 0.625rem;
}

.zoom:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

