@media only screen and (max-width: 1077px) {
    header .main-container .context-bx p {
        width: 330px;
    }

    header .main-container .context-bx .menu img {
        width: 70px;
        height: 50px;
        padding-right: 20px;
        cursor: pointer;
        transition: all .3s ease-in-out;
    }
}

@media only screen and (max-width: 842px) {
    header .main-container .context-bx {
        padding-left: 4rem;
    }

    header .main-container .context-bx h3 {
        font-size: 15px;
    }

    header .main-container .context-bx h1 {
        font-size: 1.3rem;
    }

    header .main-container .context-bx p {
        width: 210px;
    }

    header .main-container .dish-bx .dish1 {
        position: absolute;
        width: 260px;
        height: 260px;
        -webkit-filter: drop-shadow (5px 10px 10px #000);
                filter: drop-shadow (5px 10px 10px #000);
        transition: all .6s ease-in-out;
    }

    header .main-container .context-bx .menu img {
        width: 60px;
        height: 50px;
        padding-right: 10px;
    }
}


@media only screen and (max-width: 700px) {
    header .bg {
        width: 200%;
    }

    header nav li {
        padding-left: 20px;
    }

    header .main-container {
        flex-wrap: wrap-reverse;
    }

    header .main-container .context-bx,
    .dish-bx {
        width: 100%;
    }

    header .main-container .dish-bx {
        height: 40%;
    }

    header .main-container .dish-bx .dish1 {
        width: 170px;
        height: 170px;
        position: unset;
    }
}

