* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Lobster', cursive;
}
body {
    height: 100vh;
    width: 100%;

}

.container {
    position: relative;
    width: 100%;
    background-color: #fff;
    min-height: 100vh;
    overflow: hidden;
}

.forms-container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.signin-signup {
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: 75%;
    width: 50%;
    transition: 1s 0.7s ease-in-out;
    display: grid;
    grid-template-columns: 1fr;
    z-index: 5;
}

.form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0rem 5rem;
    transition: all 0.2s 0.7s;
    overflow: hidden;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
}

.sign-up-form {
    opacity: 0;
    z-index: 1;
}

.sign-in-form {
    z-index: 2;
}

.title {
    font-size: 2.6rem;
    color: #000;
    margin-bottom: 10px;
}

.input-field {
    max-width: 380px;
    width: 100%;
    background-color: #f0f0f0;
    margin: 10px 0;
    height: 55px;
    border-radius: .3rem;
    display: grid;
    grid-template-columns: 15% 85%;
    padding: 0 0.4rem;
    position: relative;
}

.input-field i {
    text-align: center;
    line-height: 55px;
    color: #000;
    transition: 0.5s;
    font-size: 1.2rem;
}

.input-field input {
    background: none;
    outline: none;
    border: none;
    line-height: 1;
    font-weight: 600;
    font-size: 1.2rem;
    color: #000;
}

.input-field input::-webkit-input-placeholder {
    color: rgb(84, 84, 84);
    font-weight: 500;
}

.input-field input::placeholder {
    color: rgb(84, 84, 84);
    font-weight: 500;
}

.social-text {
    padding: 0.7rem 0;
    font-size: 1.2rem;
}

.social-media {
    display: flex;
    justify-content: center;
}

.social-icon {
    height: 46px;
    width: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0.45rem;
    color: #000;
    border-radius: 50%;
    border: 1px solid #ffd966;
    text-decoration: none;
    font-size: 1.4rem;
    transition: 0.3s;
}

.social-icon:hover {
    /* background: #4481eb; */
    background: #ffd966;
    /* border-color: #4481eb; */
    border-color: #ffd966;
    color: white;
}

.btn {
    width: 380px;
    /* background-color: #5995fd; */
    background: #ffd966;
    border: none;
    outline: none;
    height: 49px;
    border-radius: .3rem;
    /* display: block; */
    color: #000;
    font-size: 1.4rem;
    font-weight: 600;
    margin: 10px 0;
    cursor: pointer;
    transition: 0.5s;
}

.btn.transparent:hover {
    background-color: white;
    /* color: #4d84e2; */
    color: #ffd966;
}

.btn:hover {
    /* background-color: #4d84e2; */
    background: #ffd966;
}

.panels-container {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.container:before {
    content: "";
    position: absolute;
    height: 2000px;
    width: 2000px;
    top: -10%;
    right: 48%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    /* background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%); */
    background-image: linear-gradient(-45deg, #ffd966 0%, #e6c152 100%);

    transition: 1.8s ease-in-out;
    border-radius: 50%;
    z-index: 6;
}

.image {

    width: 100%;
    transition: -webkit-transform 1.1s ease-in-out;
    transition: transform 1.1s ease-in-out;
    transition: transform 1.1s ease-in-out, -webkit-transform 1.1s ease-in-out;
    transition-delay: 0.4s;
}

.panel {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    text-align: center;
    z-index: 6;
}

.left-panel {
    pointer-events: all;
    padding: 3rem 17% 2rem 12%;
}

.right-panel {
    pointer-events: none;
    padding: 3rem 12% 2rem 17%;
}

.panel .content {
    color: #000;
    transition: -webkit-transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out;
    transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
    transition-delay: 0.6s;
}

.panel h3 {
    font-weight: 600;
    line-height: 1;
    font-size: 1.7rem;
}

.panel p {
    font-size: 1.4rem;
    padding: 0.7rem 0;
}

.btn.transparent {
    margin: 0;
    background: none;
    border: 2px solid #fff;
    width: 130px;
    height: 41px;
    font-weight: 600;
    font-size: 1.4rem;
}

.right-panel .image,
.right-panel .content {
    -webkit-transform: translateX(800px);
            transform: translateX(800px);
}

/* ANIMATION */

.container.sign-up-mode:before {
    -webkit-transform: translate(100%, -50%);
            transform: translate(100%, -50%);
    right: 52%;
}

.container.sign-up-mode .left-panel .image,
.container.sign-up-mode .left-panel .content {
    -webkit-transform: translateX(-800px);
            transform: translateX(-800px);
}

.container.sign-up-mode .signin-signup {
    left: 25%;
}

.container.sign-up-mode .sign-up-form {
    opacity: 1;
    z-index: 2;
}

.container.sign-up-mode .sign-in-form {
    opacity: 0;
    z-index: 1;
}

.container.sign-up-mode .right-panel .image,
.container.sign-up-mode .right-panel .content {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.container.sign-up-mode .left-panel {
    pointer-events: none;
}

.container.sign-up-mode .right-panel {
    pointer-events: all;
}

@media (max-width: 870px) {
    .container {
        min-height: 800px;
        height: 100vh;
    }

    .signin-signup {
        width: 100%;
        top: 95%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
        transition: 1s 0.8s ease-in-out;
    }

    .signin-signup,
    .container.sign-up-mode .signin-signup {
        left: 50%;
    }

    .panels-container {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 2fr 1fr;
    }

    .panel {
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        padding: 2.5rem 8%;
        grid-column: 1 / 2;
    }

    .right-panel {
        grid-row: 3 / 4;
    }

    .left-panel {
        grid-row: 1 / 2;
    }

    .image {
        width: 200px;
        transition: -webkit-transform 0.9s ease-in-out;
        transition: transform 0.9s ease-in-out;
        transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
        transition-delay: 0.6s;
    }

    .panel .content {
        padding-right: 15%;
        transition: -webkit-transform 0.9s ease-in-out;
        transition: transform 0.9s ease-in-out;
        transition: transform 0.9s ease-in-out, -webkit-transform 0.9s ease-in-out;
        transition-delay: 0.8s;
    }

    .panel h3 {
        font-size: 1.4rem;
    }

    .panel p {
        font-size: 1rem;
        padding: 0.5rem 0;
    }

    .btn.transparent {
        width: 110px;
        height: 35px;
        font-size: 1.4rem;
    }

    .container:before {
        width: 1500px;
        height: 1500px;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        left: 30%;
        bottom: 68%;
        right: initial;
        top: initial;
        transition: 2s ease-in-out;
    }

    .container.sign-up-mode:before {
        -webkit-transform: translate(-50%, 100%);
                transform: translate(-50%, 100%);
        bottom: 32%;
        right: initial;
    }

    .container.sign-up-mode .left-panel .image,
    .container.sign-up-mode .left-panel .content {
        -webkit-transform: translateY(-300px);
                transform: translateY(-300px);
    }

    .container.sign-up-mode .right-panel .image,
    .container.sign-up-mode .right-panel .content {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    .right-panel .image,
    .right-panel .content {
        -webkit-transform: translateY(300px);
                transform: translateY(300px);
    }

    .container.sign-up-mode .signin-signup {
        top: 5%;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }
}

@media (max-width: 570px) {
    .form {
        padding: 0 1.5rem;
    }

    .image {
        display: none;
    }

    .panel .content {
        padding: 0.5rem 1rem;
    }

    .container {
        padding: 1.5rem;
    }

    .container:before {
        bottom: 72%;
        left: 50%;
    }

    .container.sign-up-mode:before {
        bottom: 28%;
        left: 50%;
    }
}
